nav{
    background-color: rgba(128, 220, 230, 0.1);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 1000;
    position: fixed;
    left:50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background-color: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: white;
    font-size: 1.1rem;
    transition: all 0.5s ease-in;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.8);
   
}





