.about_container {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    place-items: center;
    position: relative;

    @media screen and (max-width: 700px) {
        grid-template-columns: 1fr;
    }

    .about_left {
        display: flex;
        position: relative;
        background: #20c997;
        max-width: 350px;
        height: 350px;
        justify-self: center;
        border-radius: 5px;
        box-shadow: 0 0 20px #20c997;
      

      

        img {
            object-fit: cover;
            width: 100%;
            height: 100%;
            z-index: 100;
            border-radius: 5px;
        }
        
    }

    .about_left p{
        position: absolute;
        bottom: 0.45rem;
        font-size: 2rem;
        font-weight: 600;
        left: 1.8rem;
        z-index: 1;
        color: #fff;
        font-family: Arial, Helvetica, sans-serif;
        // text-shadow: 0 0 2px rgba(51, 51, 51, 0.537);
        

    }
    .about_right {
        color: #fff;
        padding: 1rem;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 700px) {
            padding: 0;
            margin-top: 2rem;
        }
        
        p {
            font-size: 0.85rem;
            margin-bottom: 1rem;max-width: 450px;
            text-align: justify;
            line-height: 1.8;
            letter-spacing: 1px;
        }
        .bio {
            display: flex;
            font-size: 0.85rem;
            max-width: 200px;
            margin-bottom: 1rem;

            .bioKey {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                margin-right: 3rem;
            }
        }
        a {
            
                text-decoration: none;
                color: #000;
                background: #21D49E;
                padding: 0.65rem 1rem;
                border-radius: 10px;
                font-size: 0.85rem;
                text-transform: capitalize;
                width: 165px;
        }
    }
    
}


